import React from "react";
import {Link} from "react-router-dom";


const Header = () => {
  return (
    <>
      <div className=" md:mx-20 ">
       
        <h4 className=" font-bold  mt-8 mb-4 text-xl ">Create Flashcard</h4>
        <div className="ml-2 ">
          <div className=" flex items-centre mt-2  ">
            <div>
              <Link
                className="mr-14 font-bold text-slate-500 hover:text-green-600  "
                to="/"
              >
                Create New
              </Link>
            </div>
            <div>
              <Link
                className="font-bold text-slate-500 hover:text-green-600 "
                to="/myflashcard"
              >
                My Flashcard
              </Link>
            </div>
          </div>
          <div className="mt-1 border border-gray-300 border-solid"></div>
        </div>
      </div>
    </>
  );
};

export default Header;
