import React from "react";
import {Link} from "react-router-dom";


const MyFlashcard = () => {
  const FlashcardDeta = JSON.parse(localStorage.getItem("flashCard"));

  return (
    <>
      <div className=" cards-main mt-20 grid items-center justify-center grid-cols-3">
        {!FlashcardDeta && <p>No Card Found.</p>}
        {FlashcardDeta?.map((card, index) => (
          <div
            key={index}
            className=" cards bg-white border-solid  w-72 h-44 px-5 pt-5 relative mx-5 mb-16  "
          >
            <div className="flex items-center justify-center mt-5">
              <div>
                <img
                  className = "w-24 h-24 bg-indigo-100 rounded-full  absolute -top-10 left-36 "
                  src={card.group.image}
                  alt = "Img"
                />
              </div>
              <div className="mt-5 ">
                <h3 className="font-bold text-2xl ">{card.group.groupName} </h3>
               
              </div>
            </div>

            <div>
              <p className=" card-para mt-2 mb-4  text-center  ">
                {card.group.description}
              </p>
            </div>
            <div className="flex text-center justify-center ">
                  <div className="flex text-md font-bold text-gray-600 ">
                    <p className="mr-1  ">{card.terms.length} </p>
                    <p className="">
                      {card.terms.length === 1 ? <p>Card</p> : <>Cards</>}{" "}
                    </p>
                  </div>
                </div>
            <div className="mt-16">
              <Link
                className="mr-14 font-bold text-gray-600 hover:text-green-600  "
                to={`card${card.id}/term1`}
              >
                {card.terms.length === 1 ? (
                  <p className="text-center">
                    View Card 
                  </p>
                ) : (
                  <p className=" text-center">
                    View Cards
                  </p>
                )}
              </Link>
            </div>
          </div>
        ))}
      </div>
    </>
  );
};

export default MyFlashcard;
